<template>
<main>
  <Header :header="content"/>
        <div class="container top">
            <p>
            <input type="text" placeholder="کد قطعه را وارد نمایید">
            <button class="btn btn-primary">چک کردن</button>
            </p>

            <div>
                <p class="mx-5"> نام ست : ست اتاق عمل</p>
                <p> تعداد قطعات ست : 2 </p>
            </div>
            
        </div>

        <div class="container table">
            <div class="list">
                <table>
                    <tr :class="[status ? 'green':'red']">
                        <td>
                            <div>قطعه شماره 1</div>
                            <button class="btn btn-danger" v-if="status==false" @click="showModal"  data-bs-toggle="modal" data-bs-target="#staticBackdrop">توضیحات</button>
                        </td>
                    </tr>
                    <tr :class="[status ? 'green':'red']">
                        <td>
                            <div>قطعه شماره 1</div>
                            <button class="btn btn-danger" v-if="status==false" @click="showModal"  data-bs-toggle="modal" data-bs-target="#staticBackdrop">توضیحات</button>
                        </td>
                    </tr>
                    
                    
                </table>
            </div>
            <div class="image">
                <img :src="require(`../../../assets/${img}.png`)" alt="نمایش عکس قطعه" >
            </div>
        </div>


        <div class="container">
            <button class="btn btn-success" @click="showModal1"  data-bs-toggle="modal" data-bs-target="#staticBackdrop1">تحویل کامل</button>
            <button class="btn btn-danger  mx-5"  @click="showModal2"  data-bs-toggle="modal" data-bs-target="#staticBackdrop2">تحویل ناقص</button>
        </div>


<!-- *********مدال توضیحات********* -->
<div class="modal fade"  ref="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content" style="background-color:white;border:2px solid black">
      <div class="modal-body" style="margin:auto;border:none"> 
        <textarea name="" id="" cols="50" rows="10" class="mt-5 p-3"  placeholder="توضیحات مربوطه را اینجا بنویسید ..."></textarea>
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button type="button" class="btn btn-success mx-3" >ثبت</button>
        <button type="button" class="btn btn-primary mx-3" data-bs-dismiss="modal">بستن</button>
      </div>
    </div>
 </div>
</div>

<!-- *********مدال تحویل کامل********* -->  
<div class="modal fade"  ref="staticBackdrop1" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content" style="background-color:#F0FFFF;border:2px solid #2DA839">
      <div class="modal-header" style="border: none;">
        <img src="../../../assets/check.png" alt="" style="margin:auto;width:50px;height:50px;">
      </div>
      <div class="modal-body" style="margin:auto;border:none"> 
        ست اتاق عمل به صورت کامل دریافت گردید
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button type="button" class="btn btn-primary mx-3" data-bs-dismiss="modal">بستن</button>
      </div>
    </div>
  </div>
</div>

<!-- *********مدال تحویل ناقص********* -->  
<div class="modal fade"  ref="staticBackdrop2" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content" style="background-color:#FFDFDF;border:2px solid red">
      <div class="modal-header" style="border: none;">
        <img src="../../../assets/remove.png" alt="" style="margin:auto;width:50px;height:50px;">
      </div>
      <div class="modal-body" style="margin:auto;border:none"> 
        ست اتاق عمل به صورت ناقص دریافت گردید
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button type="button" class="btn btn-primary mx-3" data-bs-dismiss="modal">بستن</button>
      </div>
    </div>
  </div>
</div>

</main>
</template>

<script>
import Header from '../../../components/Header.vue'
import {Modal} from 'bootstrap'
export default {
    name:'CheckDirty',
    components:{Header},
    data() {
        return {
            content:"بررسی قطعات ست",
            img:"t",
            status:false,
            modal:null,
            modal1:null,
            modal2:null,
        }
    },
    mounted(){
      this.modal=new Modal(this.$refs.staticBackdrop);
      this.modal1=new Modal(this.$refs.staticBackdrop1);
      this.modal2=new Modal(this.$refs.staticBackdrop2);
    },
    methods: {

      showModal(){
        this.modal.show()
      },
      showModal1(){
        this.modal1.show()
      },
      showModal2(){
        this.modal2.show()
      },
    
    }
}
</script>

<style lang="scss" scoped>
@import '../../../style/check-set.scss'
</style>